













import { Component, Provide, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Article extends Vue {
    @Prop() private articleValue!: any;
    @Provide() msg: string = '';

    goDetail() {
        this.$router.push({
            name:'detail',
            params: {
                id: this.articleValue.id
            }
        })
    }
}
